<template> 
<div class="login">
		<div class="d-flex align-items-center login-conainer justify-content-center">
			<div class="login__panel ">
				<div class="login__brand d-flex justify-content-center">
					<div class="login__brand-container">
						<img src="../assets/logo.png" class="login__brand-logo" alt="">
							<h2 class="login__brand-name">ЖКХ</h2>
						</div>
					</div>
					<form class="login-form">
                 <p class="login__error" v-if="error">{{error}}</p>
              <div class="login-form__input">
							<input type="text"  id="login-name" class="login-form__input-fileds" value="" onkeyup="this.setAttribute('value', this.value); " v-model="username" >
								<label for="login-name" class="float-label">Ваше Имя</label>
							</div>
						<div class="login-form__input">
							<input type="email"  id="login-email" class="login-form__input-fileds"  value="" onkeyup="this.setAttribute('value', this.value);" v-model="email">
								<label for="login-email" class="float-label">Email</label>
							</div>
							<div class="login-form__input">
								<input type="password"  id="login-password" class="login-form__input-fileds"   value="" onkeyup="this.setAttribute('value', this.value);" v-model="password"> 
									<label for="login-password" class="float-label">Пароль</label>
								</div>
                      <div class="login-form__input">
								<input type="password" id="login-сpassword" class="login-form__input-fileds"  value="" onkeyup="this.setAttribute('value', this.value);">
									<label for="login-сpassword" class="float-label">Подтвердите Пароль</label>
								</div>
								<!--<div class="login-form__linecheckbox d-flex align-items-center">
									<div class="login-form__checkbox ">
										<input type="checkbox" id="login-checkbox" class="login-form__checkbox-input" name="remember">
											<label for="login-checkbox" class="login-form__checkbox-label">Запомнить меня</label>
										</div>
										<div class="login-form__fogot">
											<a href="#">Забыли пароль ?</a>
										</div>
									</div>-->
                  <div class="login-form__submitbutton">
                    <button type="submit" class="btn-primary button-max" @click.prevent="SignUp" :disabled="loading">Зарегистрируйтесь</button>
                  </div>
								</form>
                <p class="login__still">Есть аккаунт? <router-link to="/" >Войдите.</router-link></p>
							</div>
						</div>
					</div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      email: "",
      password: ""
    };
  },
  computed: {
    error() {
      return this.$store.getters.error;
    },
    loading() {
      return this.$store.getters.loading;
    }
  },
  methods: {
    closeError() {
      this.$store.dispatch("cleanError");
    },
    SignUp() {
      const user = {
        email: this.email,
        password: this.password
      };
      this.$store
        .dispatch("registerUser", user)
        .then(() => {
          this.$router.push("/");
        })
        .catch(() => {});
    }
  }
};
</script>
